/************** CreateFood **************/

#createFoodConfirmBG {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 2;
    display: none;
    opacity: 0;
    touch-action: none;
    transition: opacity 350ms;
  }
  #createFoodConfirmContainer {
    position: fixed;
    bottom: 0%;
    left: 50%;
    transform: translate(-50%, 50%);
    width: 100%;
    max-width: calc(var(--max-width) - 20%);
    padding: 20px;
    background-color: var(--background-white);
    border-radius: 5px;
    z-index: 2;
    text-align: center;
    box-shadow: 5px 5px 7px -6px rgba(0, 0, 0, 0.55);
    display: none;
    opacity: 0;
    transition: opacity 250ms, bottom 100ms;
  }
  #createFoodConfirmContainer p {
    margin: auto;
    margin-bottom: 35px;
    color: var(--text-kinda-black);
    font-size: calc(17px + 6 * ((var(--vw) - 320px) / 680));
    text-align: center;
  }
  #createFoodConfirmContainer button {
    font-family: inherit;
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
    color: white;
    background-color: red;
    padding: 3px 50px;
    border: none;
    border-radius: 5px;
    margin: 0 20px;
    cursor: pointer;
  }
  #createFoodConfirmContainer button:nth-child(3) {
    background-color: var(--main-color);
  }
  #createFoodConfirmContainer button:hover {
    color: var(--text-kinda-black);
    font-weight: bold;
  }
  #createFoodPage {
    width: 100%;
    max-width: var(--max-width);
    min-width: 320px;
    margin: auto;
    padding-bottom: 20px;
    transition: bottom 250ms, opacity 250ms;
    background-color: var(--background-white);
  }
  #createFoodHeader {
    position: sticky;
    top: 0;
    z-index: 1;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 70px;
    background-color: var(--main-color);
  }
  #createFoodBack {
    margin-left: 20px;
    width: 35px;
    height: 35px;
    fill: white;
    cursor: pointer;
  }
  #createFoodBack:hover {
    fill: black;
  }
  #createFoodHeaderButtonsContainer {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    color: white;
    overflow: hidden;
    font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    cursor: pointer;
    text-align: center;
    margin: 0;
    margin-right: 15px;
  }
  #CreateMealTrashCan {
    fill: white;
    width: 35px;
    height: 35px;
    margin: 0 20px;
    cursor: pointer;
  }
  #CreateMealTrashCan:hover {
    fill: black;
  }
  #CreateMealSave {
    fill: white;
    width: 35px;
    height: 35px;
    margin-left: 20px;
    margin-right: 1%;
    cursor: pointer;
  }
  #CreateMealSave:hover {
    fill: black;
  }
  .createFoodItemsContainer {
    position: relative;
    border: solid 2px var(--main-color);
    border-radius: 10px;
    margin: 35px auto;
    width: 90%;
  }
  .createFoodTitles {
    position: absolute;
    top: -20px;
    right: 20px;
    padding: 0 5px;
    background-color: var(--background-white);
    font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    color: var(--text-kinda-black);
    white-space: nowrap;
    height: 32px;
    margin: 0;
  }
  .createFoodUnits {
    position: absolute;
    left: 15px;
    bottom: 50%;
    transform: translate(0, 50%);
    margin: 0;
    text-align: center;
    color: var(--text-gray);
  }
  #foodName {
    display: block;
    margin: 0.5px auto;
    height: 55px;
    width: 99%;
    color: var(--text-kinda-black);
    font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    text-align: center;
    border: none;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0);
  }
  #foodBrand {
    display: block;
    margin: 0.5px auto;
    height: 55px;
    width: 99%;
    color: var(--text-kinda-black);
    font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    text-align: center;
    border: none;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0);
  }
  #foodKcal {
    display: block;
    margin: 0.5px auto;
    height: 55px;
    width: 99%;
    color: var(--text-kinda-black);
    font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    text-align: center;
    border: none;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0);
  }
  #foodServingValue {
    display: block;
    margin: 0.5px auto;
    height: 55px;
    width: 99%;
    color: var(--text-kinda-black);
    font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    text-align: center;
    border: none;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0);
  }
  #chooseCategorie {
    display: block;
    margin: 0.5px auto;
    height: 55px;
    width: 99%;
    font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    border: none;
    border-radius: 10px;
    background-color: var(--background-white);
    color: var(--text-kinda-black);
  }
  #createFoodNutritionInfoDetail {
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    padding-bottom: 20px;
    background-color: var(--background-white);
  }
  .createFoodNutritionInfoSub {
    font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
    margin: 10px 0;
    color: var(--text-kinda-black);
    height: 35px;
  }
  .createFoodNutritionInfoMain {
    font-weight: bold;
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
    color: var(--text-kinda-black);
    margin: 20px 0;
    height: 35px;
  }
  #createFoodNutritionInfoValues {
    direction: ltr;
  }
  #createFoodNutritionInfoValues input {
    padding: 0px;
    width: 25%;
    margin-left: 5px;
    color: var(--text-kinda-black);
    background-color: var(--background-white);
    font-weight: bold;
    font-size: inherit;
    text-align: center;
    border: none;
    border-bottom: solid 1.5px var(--main-color);
    border-radius: 0px;
  }
  @media (max-width: 450px) {
    #CreateMealTrashCan {
      width: 30px;
      height: 30px;
    }
    #CreateMealSave {
      width: 30px;
      height: 30px;
    }
  }
  